import Vue from 'vue'
import Vuex from 'vuex'
import { getStructureAPI, deleteStructureAPI, addStructureAPI } from '@/apis/StructureAPI'

Vue.use(Vuex)

const StructureStore = {
    state: {
        StructureList:[]
    },

    actions: {
        async initStructure(context) {
            const res = await getStructureAPI()
            context.commit('INITSTRUCTURE', res)
        },
        async deleteStructure(context, structure) {
            await deleteStructureAPI(structure)
        },
        async addStructure(context, structure) {
            await addStructureAPI(structure)
        },
    },

    mutations: {
        INITSTRUCTURE(state, res){
            state.StructureList = res
            console.log('初始化结构物数据完成!')
        },
        addStructure(state, structure){
            state.StructureList.push(structure)
            localStorage.setItem("Structure", JSON.stringify(state.StructureList))
            console.log('新增结构物数据成功!')
        },
        editStructure(state, structure) {
            const index = state.StructureList.findIndex(item => item.id === structure.id)
            state.StructureList[index].name = structure.name
            state.StructureList[index].location = structure.location
            state.StructureList[index].jingdu = structure.jingdu
            state.StructureList[index].weidu = structure.weidu
            localStorage.setItem("Structure", JSON.stringify(state.StructureList))
            console.log('编辑结构物数据成功!')
        },
        // DELETESTRUCTURE(state, structure) {
        //     state.StructureList.splice(state.StructureList.indexOf(structure), 1)
        //     localStorage.setItem("Structure", JSON.stringify(state.StructureList))
        //     console.log('删除结构物数据成功!')
        // }

    },

}

export default StructureStore