// 根据权限匹配动态路由

export function matchRoutes(userRoute = [], allRoute = []) {
    let matchRoute = []

    allRoute.forEach((oneRoute) => {
        userRoute.forEach((showRoute) => {
            if(showRoute.name === oneRoute.meta.name) {
                let deepCopyRoute = {...oneRoute} // 深拷贝，防止改变dynamicRoutes
                if (showRoute.children && showRoute.children.length > 0) {
                    deepCopyRoute.children = matchRoutes(showRoute.children,deepCopyRoute.children) // 这里会让allRoute改变
                }
                matchRoute.push(deepCopyRoute) 
            }
        })
    })
    return matchRoute
}

// 如果有多层嵌套，父路由就默认显示第一个子路由
export function setDefaultRoute(routes) {
    routes.forEach((route) => {
        if (route.children && route.children.length > 0) {
            route.redirect = {name: caches.children[0].name}
            setDefaultRoute(route.children)
        }
    })
    return routes
}
