import VueRouter from "vue-router";
// import {Message} from "element-ui"

// 静态路由
const routes = [
    {
        path: "/",
        component: () => import("@/pages/Layout.vue"),
        meta: {
            name: "主页",
            requireAuth: false
        }
    },
    {
        path: "/Login",
        component: () => import("@/pages/MyLogin.vue"),
        meta: {
            name:"登录",
            requireAuth: false
        }
    },
    {
        path: "/DashBoard",
        component: () => import("@/pages/DashBoard.vue"),
        meta: {
            name:"大屏",
            requireAuth: true
        }
    },
    {
        path: "/MyTest",
        component: () => import("@/components/mainPage/MyTest.vue"),
        meta: {
            name:"测试",
            requireAuth: false
        }
    }
  ]




/*
 * 根据路由权限不同，所能看到的页面和操作也不同
 *  admin-都能看到
 *  vip-能看到大部分页面
 *  user-只能看到部分页面
 * 
 *  使用addRouter()方法添加路由
 *  使用removeRouter()方法删除路由
 *  使用resetRouter()方法重置路由(删除所有路由，添加默认路由)
 */

// 准备动态加载的路由
export const dynamicRoutes = [
    {
        path: "/",
        component: () => import("@/pages/Layout.vue"),
        meta: {
            name: "主页",
            requireAuth: false
        },

        children: [
            {
                path: "websocket",
                component: () => import("@/components/mainPage/MyWebSocket.vue"),
                meta: {
                    name:"Websocket测试",
                    requireAuth: true
                }
            },
            {
                path: "MyPromise",
                component: () => import("@/components/mainPage/MyPromise.vue"),
                meta: {
                    name:"Promise测试",
                    requireAuth: true
                }
            },
            {
                path: "MyCss",
                component: () => import("@/components/mainPage/MyCss.vue"),
                meta: {
                    name:"Css测试",
                    requireAuth: true
                }
            },
            {
                path: "MyStructure",
                component: () => import("@/components/mainPage/MyStructure.vue"),
                meta: {
                    name:"结构物设置",
                    requireAuth: true
                }
            },
            {
                path: "AuthorityManagement",
                component: () => import("@/components/mainPage/AuthorityManagement.vue"),
                meta: {
                    name:"用户权限设置",
                    requireAuth: true
                }
            },
        ]
    },
    {
        path: "/Login",
        component: () => import("@/pages/MyLogin.vue"),
        meta: {
            name:"登录",
            requireAuth: false
        }
    },
    {
        path: "/DashBoard",
        component: () => import("@/pages/DashBoard.vue"),
        meta: {
            name:"大屏",
            requireAuth: true
        }
    },
    {
        path: "/MyTest",
        component: () => import("@/components/mainPage/MyTest.vue"),
        meta: {
            name:"测试",
            requireAuth: false
        }
    }
]

// 重置路由
export function resetRoutes() {
    router.matcher = new VueRouter({
        routes: routes
    }).matcher
}


