import Vue from 'vue'
import Vuex from 'vuex'
import { LoginAPI } from '@/apis/LoginAPI.js'
import { getAllUsersAPI } from "@/apis/AdminAPI";



Vue.use(Vuex)

const UserStore = {
    state: {
        user:{},
        token:'',
        auth: [],
        allUsers: []
    },

    actions: {
        async login(context, user){
            try {
                let res = await LoginAPI(user.account, user.pwd)
                console.log('actions-login输出:', res)
                if (res.token) {
                    context.commit('LOGIN', res)
                    return true
                }
                else return false
            } catch (err) {
                console.log('actions-login错误输出:', err)
            }
        },
        logout(){
            localStorage.removeItem('userInfo')
            localStorage.removeItem('token')
            localStorage.removeItem('auth')
        },
        async getAllUsers(context) {
            let allUsersInfo = await getAllUsersAPI()
            context.commit('GETALLUSERSINFO', allUsersInfo)
            console.log('actions-getAllUsers输出:', allUsersInfo)
        }
    },

    mutations: {
        GETUSER(state){
            state.user = JSON.parse(window.localStorage.getItem('userInfo')) || {}
            state.token = JSON.parse(window.localStorage.getItem('token')) || {}
            state.auth = JSON.parse(window.localStorage.getItem('auth')) || []
        },
        LOGIN(state, res){
            state.user = res
            state.token = res.token
            state.auth = res.Routes
            window.localStorage.setItem('userInfo', JSON.stringify(state.user))
            window.localStorage.setItem('token', JSON.stringify(state.token))
            window.localStorage.setItem('auth', JSON.stringify(state.auth))
        },
        GETALLUSERSINFO(state, allUsersInfo) {
            state.allUsers = allUsersInfo
        }
        // editAuthority(state, user) {
        //     const index = state.users.findIndex(item => item.id === user.id)
        //     const curIndex = state.users.findIndex(item => item.id === state.curUser.id)
        //     state.users[index].form = user.form
        //     localStorage.setItem("UserInfo", JSON.stringify(state.users))
        //     state.curUser = state.users[curIndex]
        //     window.localStorage.setItem('curUser', JSON.stringify(state.curUser))
        // }
    },



}

export default UserStore