import http from '../utils/http.js'

export function getStructureAPI() {
    return http.get('/structure') // 请求地址+请求参数，?后面的可以在服务端用query获取
}

export function deleteStructureAPI(data) {
    return http.post('/deleteStructure', data) // 请求地址+请求参数，?后面的可以在服务端用query获取

}

export function addStructureAPI(data) {
    return http.post('/addStructure', data) // 请求地址+请求参数，?后面的可以在服务端用query获取

}