import axios from 'axios'
import {Message} from 'element-ui'

let http = axios.create({
    baseURL: 'http://localhost:3300',
    timeout: 3000
})

// 请求拦截器
http.interceptors.request.use( 
    config => {
        // 从store中获取用户信息中的token
        const token = localStorage.getItem('token')        
        if (token) {
            config.headers.authorization = token
            console.log('token:', token);
        }

        return config
    },
    err => {
        return Promise.reject(err)
    }
)

// 响应拦截器
http.interceptors.response.use( 
    res => {
        console.log('返回信息:',res)
        return res.data
    },
    err => {
        console.log('出错！:', err)
        // 根据错误状态返回信息
        // 可以用err.response.status获取状态码
        if (err && err.response) {
            switch(err.response.status) {
                case 400: 
                    err.message = '请求出错'
                    break
                case 401:
                    Message.warning('授权失败，请重新登录')
                    // store中退出登录
                    setTimeout(() => {
                        window.location.reload() // 重新加载yrl，相当于重新请求页面
                    }, 1000)
                    return
                case 403:
                    err.message = '拒绝访问'
                    break
                case 404:
                    err.message = `找不到资源`
                    break
                case 500:
                    err.message = '服务器错误'
                    break
            } 
        } else { // 服务器没有返回res.response
            err.message = '连接服务器失败'
        }
        Message.error({
            message:err.response
        })
        return Promise.reject(err.response)
    }
)

export default http